body {
  box-sizing: border-box;
  --active-color: #00acdc;
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.container {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.logo, .logo img {
  display: block;
}

.nav__list {
  display: flex;
}

.nav_item + .nav_item {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

[data-qa="hover"]:hover {
  color: var(--active-color);
}

.nav__link.is-active {
  color: var(--active-color);
  position: relative;
}

.nav__link.is-active:before {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
